import { FC, useEffect, useState } from 'react';

import { withRouter } from 'next/router';

import styled from 'styled-components';

import { Box, Text } from '@openfin/ui-library';

import { getUrlParams, Router } from '@common/utils/router';

import type { AboutPageConfig } from '@client-platform/shapes';

interface Props {
    router: Router;
}

export const AboutPagePopup: FC<Props> = ({ router }) => {
    const [data, setData] = useState<AboutPageConfig | undefined>(undefined);

    useEffect(() => {
        setData(getUrlParams(router.asPath) as AboutPageConfig);
    }, [router.asPath]);

    return (
        <Container>
            <Section>
                {data?.logoUrl && <Logo src={data.logoUrl} alt="logo" />}
                <Header size="xlarge" weight="bold" title={data?.productTitle}>
                    {data?.productTitle}
                </Header>
                <TextLine weight="bold" title={data?.productVersion}>
                    {data?.productVersion}
                </TextLine>
            </Section>
            <Section>
                <TextLine weight="bold" title={data?.email}>
                    {data?.email}
                </TextLine>
                <TextLine weight="bold" title={data?.websiteUrl}>
                    {data?.websiteUrl}
                </TextLine>
            </Section>
            <Section>
                <TextSecondary title={data?.hereVersion}>Here® Core {data?.hereVersion}</TextSecondary>
                <TextSecondary title={data?.rvmVersion}>Here® Core RVM {data?.rvmVersion}</TextSecondary>
            </Section>
            {data?.copyrightMessage && (
                <Section>
                    <TextSecondary title={data?.copyrightMessage}>{data?.copyrightMessage}</TextSecondary>
                </Section>
            )}
        </Container>
    );
};

const Container = styled(Box)`
    gap: ${({ theme }) => theme.px.large};
    flex-direction: column;
    padding: ${({ theme }) => theme.px.xxlarge};
`;

const Section = styled(Box)`
    flex-direction: column;
    width: 100%;
    align-items: center;
    gap: ${({ theme }) => theme.px.xsmall};
`;

const TextLine = styled(Text)`
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: ${({ theme }) => theme.px.large};
`;

const Header = styled(TextLine).attrs({ size: 'xlarge', weight: 'bold' })`
    line-height: 1.5;
`;

const TextSecondary = styled(TextLine)`
    color: ${({ theme }) => theme.palette.textInactive};
`;

const Logo = styled.img`
    height: ${({ theme }) => theme.px.xxxxlarge};
`;

export default withRouter(AboutPagePopup);
